import { useEffect, useState } from 'react'
import Navbar from '../components/Navbar'
import Card from '../components/Card'
import Link from 'next/link'

import { summarizeFeedback } from '../lib/feedback'
import { adaptExistingPlan } from '../lib/trainingEngine'

export default function Dashboard() {
  const [plan, setPlan] = useState(null)
  const [profile, setProfile] = useState(null)
  const [feedbackSummary, setFeedbackSummary] = useState(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const savedProfile = localStorage.getItem('ft_profile')
    if (savedProfile) setProfile(JSON.parse(savedProfile))

    fetch('/api/plan')
      .then(r => r.json())
      .then(p => {
        setPlan(p)
        setLoading(false)
      })
      .catch(() => setLoading(false))

    const fb = summarizeFeedback({ lastN: 20 })
    setFeedbackSummary(fb)
  }, [])

  function regeneratePlan() {
    if (!plan || !profile) {
      alert("No plan or profile found.")
      return
    }

    const summary = summarizeFeedback({ lastN: 20 }) || {}
    const newPlan = adaptExistingPlan(plan, summary, profile)

    localStorage.setItem('ft_plan', JSON.stringify(newPlan))
    setPlan(newPlan)

    alert("Plan updated with adaptive progression!")
  }

  if (loading) {
    return (
      <>
        <Navbar />
        <main className="container p-8">
          <p className="text-slate-600 dark:text-slate-300">Loading dashboard...</p>
        </main>
      </>
    )
  }

  return (
    <>
      <Navbar />
      <main className="container p-8 grid md:grid-cols-3 gap-6">
        <section className="md:col-span-2">
          <Card title="Your Weekly Program">
            {!plan && <p className="text-slate-600 dark:text-slate-300">No plan yet ‚Äî create one.</p>}
            {plan && (
              <div className="grid gap-3">
                {plan.sessions.map((s, idx) => (
                  <div key={s.day} className="card">
                    <div className="flex items-center justify-between">
                      <h4 className="font-semibold">{s.day}</h4>
                      <Link href={`/day/${idx+1}`} className="small-btn">Open</Link>
                    </div>
                    <p className="text-sm text-slate-600 dark:text-slate-300 mt-2">{s.exercises.length} exercises ‚Äî {profile?.timePerSession || 40} mins</p>
                  </div>
                ))}
              </div>
            )}
          </Card>
        </section>

        <aside>
          <Card title="Profile">
            {profile ? (
              <>
                <p><strong>Goal:</strong> {profile.goal}</p>
                <p><strong>Exp:</strong> {profile.experience}</p>
                <p><strong>Days/week:</strong> {profile.daysPerWeek}</p>
                <p><strong>Time:</strong> {profile.timePerSession} mins</p>
                <p><strong>Adaptation:</strong> {profile.adaptationStyle}</p>
              </>
            ) : <p className="text-slate-600">No profile</p>}
          </Card>
          <div className="mt-4">
            <Card title="Adaptive Coach Summary">
              {!feedbackSummary && <p className="text-slate-600">No feedback yet. Start logging your sessions!</p>}
              {feedbackSummary && (
                <div className="text-sm">
                  <p><strong>Total logs:</strong> {feedbackSummary.total}</p>
                  <p><strong>Easy:</strong> {feedbackSummary.difficulty.Easy}</p>
                  <p><strong>Moderate:</strong> {feedbackSummary.difficulty.Moderate}</p>
                  <p><strong>Hard:</strong> {feedbackSummary.difficulty.Hard}</p>
                  <p><strong>Missed reps:</strong> {feedbackSummary.missedRepsCount}</p>
                </div>
              )}
            </Card>
          </div>

          <Card title="Actions" className="mt-4">
            <div className="flex flex-col gap-2">
              <button onClick={regeneratePlan} className="btn-primary text-center">Regenerate Adaptive Plan</button>
              <Link href="/onboard/goal" className="small-btn text-center">Create New Plan</Link>
              <Link href="/weekly-plan" className="small-btn text-center">View Full Plan</Link>
            </div>
          </Card>
        </aside>
      </main>
    </>
  )
}
